.nav__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav button {
  width: 180px;
  min-height: 45px;

  background-color: var(--transparent);
  border: 1px solid var(--white);
  border-radius: 5px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: inherit;
}
